<template>
	<div class="login-container">
		<el-form class="login-form" :model="ruleForm" :rules="rules" ref="ruleForm">
			<div class="title-container">
				<h3 class="title">用户登陆</h3>
			</div>
			<!-- 账号 -->
			<el-form-item prop="name">
				<!-- icon -->
				<span class="svg-container">
					<i class="el-icon-user"></i>
				</span>
				<el-input v-model="ruleForm.user_name" placeholder="请输入账号"></el-input>
			</el-form-item>
			<!-- 密码 -->
			<el-form-item prop="region">
				<!-- icon -->
				<span class="svg-container">
					<i class="el-icon-s-comment"></i>
				</span>
				<el-input v-model="ruleForm.user_pwd" placeholder="请输入密码" :type="passwords"></el-input>
				<span class="show_pwd" @click="clic_icon()">
					<img style="width: 30px;height: 30px;"
						:src="passwordtype?require('@/assets/close_eye.png'):require('@/assets/open_eye.png')">
				</span>
			</el-form-item>
			<!-- 登陆按钮 -->

			<el-button type="primary" round style="width: 100%; margin-bottom: 30px;" @click="submitForm()">登录
			</el-button>
			<span> 账号：admin 密码：123456</span>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				passwordtype: true,
				passwords: "password",
				ruleForm: {
					user_name: '',
					user_pwd: '',
				},
				rules: {
					user_name: [{
							required: true,
							message: '请输入用户名',
							trigger: 'blur'
						},
						{
							min: 3,
							max: 10,
							message: '长度在 3 到 10 个字符',
							trigger: 'blur'
						}
					],
					user_pwd: [{
							required: true,
							message: '请输入密码',
							trigger: 'blur'
						},
						{
							min: 5,
							max: 12,
							message: '长度在 5 到 12 个字符',
							trigger: 'blur'
						}
					],
				}
			}
		},
		methods: {
			clic_icon() {
				this.passwordtype = !this.passwordtype
				if (this.passwordtype) {
					this.passwords = 'password'
				} else {
					this.passwords = "text"
				}
			},
			submitForm() {
			  // console.log(formName)
			  // console.log(this.$refs.ruleForm)
			  this.$refs.ruleForm.validate((valid) => {
				if (valid) {
				  // alert('submit!');
				  // this.$router.push('/home')
				  // 调用登录接口
				  this.$http.post('/api/permission/getMenu',this.ruleForm).then(res=>{
					  // console.log(res);
					  res=res.data
					  // console.log(res)
					  if(res.code === 20000){
					  	//清除 防止第二次登录,重复加入菜单
					  	this.$store.commit('clearMenu')
					  	//将menu设置到vuex中
					  	this.$store.commit('setMenu',res.data.menu)
					  	//设置token
					  	this.$store.commit('setToken',res.data.token)
					  	//添加动态路由
					  	this.$store.commit('addMenu',this.$router);
						  console.log(this.$router)
					  	this.$router.push('/home')//跳转到Home目录					  	
					  }else{
					  	this.$message.error(res.data.message)
					  }
				  })
				} else {
				  console.log('error submit!!');
				  return false;
				}
			  });
			},
			resetForm(formName) {
				this.$refs[formName].resetFields();
			}
		}
	}
</script>

<!-- scoped:在style中写的样式 只对当前组件有效 -->
<style lang="scss" scoped>
	$bg: #2d3a4b; //背景色
	$dark_gray:#889aa4; //按钮颜色
	$light_gray:#eee; //输入框颜色
	$cursor:#fff; //输入光标的颜色

	.login-container {
		min-height: 100%;
		width: 100%;
		background-color: $bg;
		overflow: hidden;

		.login-form {
			position: relative;
			width: 520px;
			padding: 160px 35px 0;
			margin: 0 auto;
			overflow: hidden;
		}

		// 注意 scss中 如果想要选中elementui标签 需要使用::v-deep
		// sass中 可以使用 /deep/进行选中
		::v-deep .el-form-item {
			border: 1px solid rgba(255, 255, 255, 0.1);
			background: rgba(0, 0, 0, 0.1);
			color: #454545;
			border-radius: 5px;
		}

		::v-deep .el-input {
			display: inline-block;
			height: 47px;
			width: 85%;

			input {
				background: transparent;
				border: none;
				border-radius: 0;
				padding: 12px 5px 12px 15px;
				color: $light_gray;
				// 设置插入光标的颜色
				caret-color: $cursor
			}
		}

		.svg-container {
			padding: 6px 5px 6px 15px;
			color: $dark_gray;
			vertical-align: middle; //设置垂直对齐
			display: inline-block;
		}

		.title-container {
			position: relative;

			.title {
				font-size: 20px;
				color: $light_gray;
				margin: 0px auto 40px auto;
				text-align: center;
				font-weight: bold;
			}
		}

		.show_pwd {
			position: absolute;
			right: 10px;
			top: 7px;
			font-size: 16px;
			color: $dark_gray;
			// 鼠标浏览的位置变成小手
			cursor: pointer;
			// 阻止选中页面的文本元素
			user-select: none;
		}


	}
</style>
